.galeries {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-size: 1.5rem;
    background-color: var(--white);
    padding: 1rem 1rem;
}
.b-tab{
    width: 100%;
}
.list-grid {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, 500px);
    gap: 1rem;
}
.item-gal{
    width: 500px;
    height: 400px;
    box-shadow: var(--imgShadow);
    border-radius: 10px;
    
}
.item-gal>img{
    width: 100%;
    height: 100%;
    box-shadow: var(--imgShadow);
    border-radius: 10px;
    transition: all 0.5s ease;
    cursor: pointer;
}
.item-gal>img:hover{
    transform: scale(1.5)
}

@media screen and (max-width: 768px){
    .list-grid {
        grid-template-columns: repeat(auto-fit, 100%);
    }
    .item-gal{
        width: 100%;
    }
}