.button {
    border: none;
    height: 40px;
    width: 150px;
    padding: 5px 20px;
    font-size: 20px;
    border-radius: 10px;
    background-color: var(--blue);
    cursor: pointer;
    margin-bottom: 30px;
}

.button-outline {
    margin-top: 10px;
    border: 1px solid var(--blue);
    height: 30px;
    border-radius: 10px;
    padding: 20px 30px;
    background: none;
    color: var(--blue);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button-outline:hover {
    background-color: var(--blue);
    color: white;
}

a {
    text-decoration: none;
    font-size: 1rem;
    color: var(--blue);
}

.bg-button {
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--blue);
    color: var(--blue);
    border: none;
    border-radius: 10px;
    font-weight: bold;
    box-shadow: var(--shadow2);
    cursor: pointer;
    padding: 15px 30px;
}