.input__container {
    /* width: 250px; */
    /* height: 40px; */
    border: 1px solid var(--blue);
    border-radius: 10px;
    padding: 8px 15px;
    justify-content: flex-start;
    display: flex;
    align-items: center;
    /* margin-bottom: 30px; */
}

.input__container svg {
    color: var(--primaryColor1);
}

.input__container input,textarea {
    width: 100%;
    margin-left: 10px;
}

.input__container input,textarea {
    border: none;
}

.input__container input:focus {
    outline: none;
}
textarea:focus{
    outline: none;
}

.input__spam{
    font-size: 12px;
    padding: 3px;
    color: red;
    display: none;
}

input:invalid~span {
    display: block;
}