.Services {
    display: flex;
    flex-direction: column;
    grid-area: 1rem;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    margin-top: 6rem;
    padding: 1rem 1rem;
    background-color: var(--white);
    overflow: hidden;
    width: 100%;
}

.h-service {
    color: var(--blue) !important;
    font-weight: bold;
}

.services-contents {
    margin-top: 2.5rem;
    width: 100%;
    display: grid;
    align-items: center;
    grid-template-columns: repeat(auto-fit, 450px);
    gap: 2rem;
}

.service-item {
    width: 450px;
    height: 300px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: visible;
    border-radius: 15px;
    margin-bottom: 5rem;
    box-shadow: var(--shadow);
    cursor: pointer;
    transition: all 0.5s ease;

}

.item-service>div {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    gap: 0.5rem;
}

.icon {
    height: fit-content;
    font-size: 10rem;
    margin-top: 20px;
    color: var(--blue);
}

.service-item:hover {
    transform: scale(1.1)
}

.service-item>img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    display: block;
}

.txt-contents {
    width: 80%;
    height: 10rem;
    position: absolute;
    bottom: -50px;
    background-color: var(--blue);
    opacity: 0.8;
    border-radius: 15px;
}

.txt {
    width: 80%;
    height: 9rem;
    position: absolute;
    bottom: -40px;
    border-radius: 15px;
    text-align: center;
    box-shadow: var(--shadow);
}

.title {
    font-weight: bold;
    font-size: 1rem;
    color: var(--white);
}

.desc {
    font-size: 0.9rem;
    line-height: 20px;
    color: var(--white);
    opacity: .70;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding-left: 10px;
    padding-right: 10px;
    text-align: justify;
    margin-top: 15px;
}

@media screen and (max-width: 768px) {
    .services-contents{
        display: contents;
    }
    .service-item{
        width: 100%;
    }
    
}