.Abouts {
    display: flex;
    flex-direction: column;
    grid-area: 1rem;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    margin-top: 6rem;
    background-color: var(--white);
    padding: 1rem 1rem;
    overflow: hidden;
}

.h-abouts {
    color: var(--blue) !important;
    font-weight: bold;
}

.contents-about {
    margin-top: 2rem;
    display: flex;
    flex-direction: column;   
    padding-left: 20px;
    padding-right: 20px;
}

.h-text-about {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 50px;
}

.left-txt {
    width: 45%;
}

.desc-text {
    font-size: .9rem !important;
    opacity: 0.60;
    display: block;
    display: flex;
    justify-content: center;
    line-height: 27px;
    text-align: justify;
    margin-bottom: 25px;
    text-justify: inter-word;
}

.imgs {
    width: 45%;
    height: 400px;
    position: relative;
}

.opa-img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.opa-img button {
    margin: 0px 10px 0px 10px;
    text-align: center;
    width: 3rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--white);
    color: var(--white);
    background-color: var(--blue);
    opacity: 0.75;
    border-radius: 0.2rem;
}

.h-text-about img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    box-shadow: var(--shadow);

}

.h-text-about .opa-img:hover {
    cursor: pointer;
}
.opa-img button:hover{
    background-color: var(--white);
    color: var(--blue);
    cursor: pointer;
}


@media screen and (max-width: 768px){
    .h-text-about{
        flex-direction: column;
    }
    .left-txt, .imgs{
        width: 100%;
    }
}
