.contacts{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-size: 1.5rem;
    margin-top: 6rem;
    background-color: var(--white);
    padding: 1rem 1rem; 
    overflow: hidden;
}
.contents-contacts{
    display: flex;
    gap: 1rem;
    padding: 30px 5px;
}
.left-contacts{
    flex: 1;
    /* border: 1px solid red; */
}
.tab-bar{
    width: 70%;
    display: flex;
    flex-direction: column;
    opacity: 0.75;
}
.h-tab{
    display: flex;
    justify-content: space-between;
    
}
.item-h-tab{
    font-size: 1rem;
    padding: 10px 0px;
    flex: 1;
    cursor: pointer;
    position: relative;
}
.item-h-tab>img{
    width: 35px;
    height: 35px;
    position: relative;
    top: 12px;
    left: 10px;
}
.t-select{
    color: var(--blue);
    border-bottom: 2px solid var(--blue);
    border-radius: 3px;
}

.b-tab{
    margin-top: 2rem;
    text-align: start;
    height: fit-content;
}

.p-infos{
    padding: 20px;
    border: 5px solid var(--gray);
}
.c-infos{
    font-size: 1rem;
    display: flex;
    align-items: center;
    font-size: 0.7rem;
    margin-bottom: 1rem;
}
.c-infos>:nth-of-type(1){
    color: var(--blue);
    font-weight: bold;
}
.c-infos>span{
    margin-left: .5rem;
}

.right-contacts{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    /* border: 1px solid yellow; */
}
.form-contact{
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    /* border: 1px solid red; */
}
.input_group{
    display: flex;
    gap: 1rem;
    justify-content: end;
}
.map-ctn{
    margin-top: 20px;
    width: 100%;
    color: white;
    /* border: 1px solid red; */
}

@media screen and (max-width: 768px){
    .contents-contacts{
        flex-direction: column;
        justify-content:space-around;
        gap: 2rem;
    }
    .tab-bar{
        width: 100%;
        height: fit-content;
    }
    .item-h-tab{
        font-size: 0.7rem;
    }
    .item-h-tab>img{
        width: 24px;
        height: 24px;
        top: 9px;
    }
    .right-contacts{
        gap: 0.5rem;
    }
    .input_group{
        flex-direction: column;
        margin-top: 0px;
        justify-content:end;
    }
}