/* make variables nth-of-type(1) */
:root {
  --blue: #024f5c; /* #08468a #141e38; */
  --white: #ffffff;
  --gray: #f9f9f9;
  --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
  --shadow : rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  --imgShadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}

.App{
    display: flex;
    flex-direction: column;
    /* gap: 6rem; */
    overflow: hidden;
}

::-webkit-scrollbar{
  display: none;
}

.btn{
 /* // background-color: white; */
  padding: 0.5rem;
  font-weight: bold;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

.btn:hover{
  cursor: pointer;
}
.strock-text{
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}