.Headers {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.app-header {
    display: flex;
    background-color: var(--blue);
    height: 3rem;
    justify-content: space-between;
}

.email-icons {
    display: flex;
    gap: 1rem;
    color: var(--gray);
    align-items: center;
    margin-left: 5rem;
    margin-right: 5rem;
}

.menu {
    background-color: var(--white);
    display: flex;
    height: 6rem;
    align-items: center;
    gap: 5rem;
   font-size: 0.8rem;
   font-weight: bold;
}
.menu>img{
    width: 9rem;
    height: 5rem;
    margin-left: 1.5rem;
}

.menu>ul {
    list-style: none;
    display: flex;
    gap: 1.5rem;
}
.ietm-menu{
    display: flex;
    gap: 0.5rem;
}
.active{
    color: var(--blue);
}