.slider {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 90vh;
    background-color: var(--blue);
    overflow: hidden;
}
.op-bg,
.text-contents,
.slider>img {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    color: var(--white);
    font-size: 3rem;
    font-weight: bold;
}

.op-bg {
    background-color: var(--blue);
    opacity: 0.7;
}

.text-contents {
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items:initial;
    width: 50%;
    gap: 1rem;
    padding-left: 1rem;
}

.desc-text {
    font-size: 1.5rem;
    display: block;
}

.text-contents>button {
    width: 10rem;
    height: 3rem;
    border-radius: 1rem;
    border: 1px solid var(--white);
    color: var(--white);
}

.control-btn {
    display: flex;
    gap: 2rem;
    margin-top: 5rem;
}

.control-btn>button {
    text-align: center;
    width: 3rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--white);
    color: var(--white);
    background-color: transparent;
    border-radius: 0.2rem;
}

.text-contents>button:hover,
.control-btn>button:hover {
    background-color: var(--white);
    color: var(--blue);
}

@media screen and (max-width: 768px){
    .slider {
        height: 60vh;
    }
    .strock-text{
        font-size: 1.5rem;
    }
}